import { $ } from '../$.js';
import './on.js';
$.fn.one = function (types, 
// eslint-disable-next-line @typescript-eslint/no-explicit-any
selector, 
// eslint-disable-next-line @typescript-eslint/no-explicit-any
data, 
// eslint-disable-next-line @typescript-eslint/no-explicit-any
callback) {
    // @ts-ignore
    return this.on(types, selector, data, callback, true);
};
